<template>
<div class="container">

    <div class="searchbar">
        <from @submit.prevent="fetchSearchNews">
            <input type="text" placeholder="搜尋..." v-model="searchword" @keyup.enter="fetchSearchNews" />
        </from>
        <div class="search-icons">
            <i v-if="!isBusy" class="fas fa-search"></i>
            <i v-else class="fas fa-spinner fa-spin"></i>
            <i class="fas fa-times" @click="fetchTopNews"></i>
        </div>
    </div>
    <div class="result-list">
        <h3>今日焦點新聞</h3>
        <p>(此畫面適用於手機營幕)</p>
        <article v-for="(article, index) in articles" :key="index" @click="navTo(article.url)">
            <header>
                <img v-if="article.urlToImage" :src="article.urlToImage" alt="" />
                <i v-else class="fas fa-image"></i>
            </header>
            <section v-html="article.title"></section>
            <footer>
                <i class="fas fa-chevron-right"></i>
            </footer>
        </article>
    </div>
    <div ref="infinitescrolltrigger" id="scroll-triger">
        <i v-if="showloader" class="fas fa-spinner fa-spin"></i>
    </div>
</div>
</template>

<script>
export default {
    props: [
        'apiKey'
    ],
    data() {
        return {
            apiUrl: '', //api網址
            isBusy: false,
            showloader: false, //
            currentPage: 1, //當前頁面
            totalResults: 0, //這個api總共幾篇文章
            maxPerPage: 34, //篇數
            searchword: '', //搜尋關鍵字
            articles: [], //文章
            country: 'tw' //國家
        }
    },
    computed: {
        pageCount() {
            //Math.ceil:四捨五入
            return Math.ceil(this.totalResults / this.maxPerPage)
        }
    },
    methods: {
        //新分頁，開啟文章
        navTo(url) {
            window.open(url)
        },
        resetData() {
            this.currentPage = 1;
            this.articles = [];
        },
        fetchSearchNews() {
            if (this.searchword !== '') {
                this.apiUrl = 'https://newsapi.org/v2/everything?q=' + this.searchword +
                    '&pageSize=' + this.maxPerPage +
                    '&apiKey=' + this.apiKey;

                this.isBusy = true;

                this.resetData();
                this.fetchData();
            } else {
                this.fetchTopNews();
            }
        },
        fetchTopNews() {
            this.apiUrl = 'https://newsapi.org/v2/top-headlines?country=' + this.country +
                '&pageSize=' + this.maxPerPage +
                '&apiKey=' + this.apiKey;
            this.isBusy = true;
            this.searchword = '';

            this.resetData();
            this.fetchData();
        },
        //異步調用news api
        fetchData() {
            let req = new Request(this.apiUrl + '&page=' + this.currentPage);
            fetch(req)
                .then((resp) => resp.json())
                .then((data) => {
                    this.totalResults = data.totalResults;
                    //把每個data放進articles
                    data.articles.forEach(element => {
                        this.articles.push(element);
                    });
                    this.isBusy = false;
                    this.showloader = false;
                })
                .catch((error) => {
                    console.log(error);
                })
        },
        scrollTriger() {
            //observer:建立觀察器
            //entry:指定觀察器要觀察的獵物
            //當獵物 進入(true) / 離開(false) 觀察器視窗的範圍，觀察器就執行callback
            //使用IntersectionObserver更高效的監視某個頁面元素是否進入了可見窗口
            const observer = new IntersectionObserver((enties) => {
                enties.forEach((entry) => {
                    if (entry.intersectionRatio > 0 && this.currentPage < this.pageCount) {
                        this.showloader = true;
                        this.currentPage += 1
                        this.fetchData();
                    }
                });
            });
            observer.observe(this.$refs.infinitescrolltrigger)
        }
    },
    //顯示頁面
    created() {
        this.fetchTopNews();
    },
    mounted() {
        this.scrollTriger();
    }
}
</script>

<style lang="scss" scoped>
.container {
    position: relative;

    .searchbar {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 60px;
        font-size: 1.6rem;

        input {
            padding: 0 100px 0 20px;
            margin: 0;
            width: calc(100% - 120px);
            height: 60px;
            border: none;
            font-size: 2rem;
            color: #fff;
            background-color: #222;

            &:focus {
                outline: none;
            }
        }

        .search-icons {
            position: absolute;
            right: 20px;
            top: 20px;
            color: #fff;

            i {
                margin-left: 15px;
                cursor: pointer;
            }
        }
    }

    .result-list {
        padding-top: 60px;

        h3 {
            width: 100%;
            font-size: 24px;
            color: black;
            background-color: red;
            text-align: center;
            margin: 0;
        }

        p {
            text-align: center;
            margin: 3px;
        }
    }

    article {
        display: grid;
        grid-template-columns: 200px auto 40px;
        grid-template-rows: 100px;
        border-bottom: 1px solid #ccc;
        overflow: hidden;
        cursor: pointer;

        header {
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;

            img {
                max-width: 100%;
                height: auto;
            }

            i {
                font-size: 2rem;
            }
        }

        section {
            margin: 0;
            padding: 10px;
            height: 100px;
            font-size: 14px;
        }

        footer {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.6rem;
            color: #888;
        }
    }

    #scroll-trigger {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100px;
        font-size: 1.6rem;
    }
}
</style>
